export default {
	data() {
		return {
			money: 0,
			agree: true,
			applyMoney: "", //提现申请金
			mix: "", //最低限制提现金额
		}
	},
	onShow() {
	},
	onLoad(e) {
		console.log(e);
		this.money = e.money - 0;
		this.mix = e.mix - 0;
	},
	methods: {
		getInput(e) {
			console.log(e.detail.value);
			console.log(typeof(this.money));
			if(e.detail.value > this.money) {
				uni.showToast({
					title: '不可超过您当前可提现金额!',
					duration: 2000,
					icon: 'none',
					success: r=> {
						this.applyMoney = this.money;
					}
					
				});
			}
		},
		apply() {
			console.log(this.applyMoney);
			if(this.applyMoney < this.mix) {
				uni.showToast({
					title: '提现金额不可小于' + this.mix + "元",
					icon: 'none',
					duration: 1000
				});
			} else {
				this.api({
					method: "post",
					url: "User/user_tixian",
					data: {
						money: this.applyMoney
					}
				}).then(res=> {
					console.log(res);
					if(res.data.code == 1) { //成功
						uni.showModal({
							title: '提示',
							content: '提现申请提交成功',
							showCancel: false,
							cancelText: '',
							confirmText: '确定',
							success: res => {
								if(res.confirm) {
									uni.redirectTo({
										url: './wallet'
									})
								}
							},
							fail: () => {},
							complete: () => {}
						});
					}
				})
			}
		}
	}
}
